@font-face {
  font-family: "Arial";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/arial.woff2") format("woff2"), url("../fonts/arial.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html,
body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  scroll-behavior: smooth;
}
html {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  font-family: "Arial", "Calibri", sans-serif;
  color: #3e3e3e;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}
a {
  color: #3e3e3e;
  font-style: normal;
  text-decoration: none;
}
img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}
.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 70px;
}
@media (max-width: 899px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}
.description {
  grid-column: 2 / 3;
  grid-row: 1 / 3;
}
@media (max-width: 767px) {
  .description {
    padding-top: 20px;
  }
}
.description__text-inner {
  padding: 23px 21px 15px 20px;
  margin: 0;
  background-color: #e6e8f0;
}
.description__text-inner h3 {
  margin: 0;
  font-size: 14px;
}
.description__text {
  margin: 0;
}
.description__card {
  margin: 19px 0 18px;
}
.description__card-list {
  display: grid;
  grid-template-columns: repeat(5, minmax(auto, 134px));
  grid-template-rows: auto;
  gap: 19px 21px;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
@media (max-width: 899px) {
  .description__card-list {
    grid-template-columns: repeat(auto-fill, minmax(130px, auto));
  }
}
@media (max-width: 899px) {
  .description__card-item img {
    width: 100%;
  }
}
.description__advantages-wrap {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 17px;
  min-height: 200px;
}
@media (max-width: 767px) {
  .description__advantages-wrap {
    display: flex;
    flex-direction: column;
  }
}
.description__advantages-text {
  padding: 23px 15px 9px 18px;
  margin: 0;
  background-color: #e6e8f0;
}
.footer-nav {
  display: flex;
  flex-direction: column;
}
.footer-nav__list {
  display: flex;
  flex-direction: column;
  padding: 18px 11px 14px 30px;
  margin: 0;
  list-style-type: square;
  background-color: #303651;
  color: #ffffff;
}
.footer-nav__link {
  color: #ffffff;
}
.footer-nav__link:hover {
  opacity: 0.7;
}
.footer-nav__link:active {
  opacity: 0.3;
}
.main-footer {
  padding-top: 49px;
  padding-bottom: 25px;
}
@media (max-width: 767px) {
  .main-footer {
    padding-top: 20px;
  }
}
.main-footer__wrap {
  position: relative;
  display: grid;
  grid-template-columns: minmax(240px, 297px) minmax(250px, 297px) minmax(212px, 308px);
  column-gap: 28px;
  max-width: 960px;
  min-width: 760px;
  min-height: 180px;
  margin: 0 auto;
}
.main-footer__wrap::before {
  content: "";
  position: absolute;
  top: -25px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #3e3e3e;
}
@media (max-width: 899px) {
  .main-footer__wrap {
    max-width: 809px;
    min-width: 678px;
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }
}
@media (max-width: 767px) {
  .main-footer__wrap {
    display: flex;
    flex-direction: column;
    gap: 2px;
    max-width: 727px;
    min-width: 280px;
  }
  .main-footer__wrap::before {
    content: none;
  }
}
.main-footer__information-list {
  column-width: auto;
  column-count: 2;
  column-gap: 4%;
  padding: 18px 11px 14px 24px;
  margin: 0;
  background-color: #303651;
  list-style-type: none;
}
.main-footer__information__link {
  color: #ffffff;
}
.main-footer__information__link:hover {
  opacity: 0.7;
}
.main-footer__information__link:active {
  opacity: 0.3;
}
.main-footer__container {
  display: grid;
  grid-template-rows: repeat(2, minmax(77px, auto));
  gap: 28px;
}
@media (max-width: 899px) {
  .main-footer__container {
    grid-template-rows: repeat(2, minmax(50px, auto));
    grid-column: 1 / 3;
    gap: 0;
  }
}
.main-footer__logo-inner {
  background-color: #303651;
}
.main-footer__link-logo {
  display: block;
  width: 200px;
  height: 50px;
  margin: 5px;
}
.main-footer__link-logo:hover {
  opacity: 0.7;
}
.main-footer__link-logo:active {
  opacity: 0.3;
}
.main-header {
  padding-top: 24px;
  padding-bottom: 38px;
}
@media (max-width: 767px) {
  .main-header {
    padding-bottom: 20px;
  }
}
.main-header__wrap {
  position: relative;
  display: grid;
  grid-template-columns: 122px minmax(250px, 279px) minmax(340px, 513px);
  column-gap: 23px;
  max-width: 960px;
  min-width: 760px;
  min-height: 108px;
  margin: 0 auto;
}
.main-header__wrap::before {
  content: "";
  position: absolute;
  bottom: -19px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #3e3e3e;
}
@media (max-width: 899px) {
  .main-header__wrap {
    grid-template-columns: 122px minmax(253px, 280px) minmax(255px, 362px);
    max-width: 809px;
    min-width: 678px;
  }
}
@media (max-width: 767px) {
  .main-header__wrap {
    grid-template-columns: 1fr;
    max-width: 727px;
    min-width: 280px;
    min-height: 60px;
  }
  .main-header__wrap::before {
    content: none;
  }
}
.main-header__logo-inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 108px;
  background-color: #303651;
}
@media (max-width: 767px) {
  .main-header__logo-inner {
    flex-direction: row;
    justify-content: center;
    min-height: 40px;
  }
}
.main-header__link-logo {
  width: 122px;
  height: 36px;
  padding: 3px;
}
.main-header__information-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 108px;
  background-color: #303651;
}
@media (max-width: 767px) {
  .main-header__information-container {
    min-height: 60px;
  }
}
.main-header__contacts-list {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 21px 23px;
  margin: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .main-header__contacts-list {
    align-items: center;
    padding: 10px;
  }
}
.main-header__link {
  color: #ffffff;
}
.main-header__link--email,
.main-header__link--link {
  text-decoration: underline;
}
.main-header__link:hover {
  opacity: 0.7;
}
.main-header__link:active {
  opacity: 0.3;
}
.main-nav {
  display: flex;
  justify-content: center;
  min-height: 108px;
  background-color: #303651;
}
@media (max-width: 767px) {
  .main-nav {
    min-height: 60px;
  }
}
.main-nav__list {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 10px;
  align-items: center;
  padding: 23px;
  margin: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .main-nav__list {
    display: flex;
    flex-direction: column;
    min-height: 60px;
    padding: 10px;
  }
}
.main-nav__link {
  color: #ffffff;
}
.main-nav__link:hover {
  opacity: 0.7;
}
.main-nav__link:active {
  opacity: 0.3;
}
.sidebar {
  min-height: 335px;
  padding: 20px 7px 8px 13px;
  background-color: #303651;
}
@media (max-width: 767px) {
  .sidebar {
    text-align: center;
  }
}
.sidebar h2 {
  margin: 0;
  color: #f7f7f7;
  font-family: "Arial", "Calibri", sans-serif;
  font-size: 14px;
}
@media (max-width: 767px) {
  .sidebar h2 {
    padding-bottom: 5px;
  }
}
.sidebar__list {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .sidebar__list {
    gap: 8px;
  }
}
.sidebar__link {
  color: #f7f7f7;
}
.sidebar__link:hover {
  opacity: 0.7;
}
.sidebar__link:active {
  opacity: 0.3;
}
.social {
  background-color: #303651;
}
.social__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, 24px);
  gap: 16px;
  padding: 25px 25px 16px;
  margin: 0;
  list-style-type: none;
}
@media (max-width: 899px) {
  .social__list {
    padding: 16px;
  }
}
.social__link {
  display: block;
  width: 24px;
  height: 24px;
}
.social__icon {
  fill: #df9730;
}
.social__link:hover .social__icon {
  fill: #ffffff;
}
.social__link:active .social__icon {
  fill: #ffffff;
  opacity: 0.5;
}
.website-development__wrap {
  display: grid;
  grid-template-columns: 184px 1fr;
  grid-template-rows: minmax(335px, auto) 1fr;
  column-gap: 20px;
  max-width: 1100px;
  min-width: 900px;
  min-height: 300px;
  margin: 0 auto;
}
@media (max-width: 899px) {
  .website-development__wrap {
    max-width: 899px;
    min-width: 768px;
  }
}
@media (max-width: 767px) {
  .website-development__wrap {
    display: flex;
    flex-direction: column;
    max-width: 767px;
    min-width: 320px;
  }
}
